import { Mark, Data } from '@ali/4ever-cangjie';

export interface DstrikeData extends Data {
  value: boolean;
}

interface Dstrike extends Mark<DstrikeData> {};

const Dstrike = {
  TYPE: 'dstrike',

  create(value?: boolean): Dstrike {
    return Mark.create<DstrikeData>({
      type: Dstrike.TYPE,
      data: {
        value: !!value,
      }
    });
  },

  isDstrike(maybeDstrike: any): maybeDstrike is Dstrike {
    return Mark.isMark(maybeDstrike) && maybeDstrike.type === Dstrike.TYPE;
  },
};

export default Dstrike;
